  top: -1px;
    
  border-bottom-left-radius: 25px;
  
  height: 18vmin;
  width: 55px;
  width: 6.1vmin;

  
  border-top: rgba(0,0,0,.3) 1px solid;
  border-left: rgba(0,0,0,.3) 1px solid;

  filter: drop-shadow(15px 6px 7px rgba(0,0,0,.1));

